.ppt1-slide1-wrapper {
  width: 100%;
  height: 100%;
  background-color: #000;
  color: white;
  font-size: 56px;
}

@-webkit-keyframes far_to_near {
  0% {
    -webkit-transform: scale(0) translateY(0) rotateZ(0);
            transform: scale(0) translateY(0) rotateZ(0);
  }
  80% {
    -webkit-transform: scale(1) translateY(-68px) rotateZ(0);
            transform: scale(1) translateY(-68px) rotateZ(0);
  }
  90% {
    -webkit-transform: scale(1) translateY(-48px) rotateZ(0.02turn);
            transform: scale(1) translateY(-48px) rotateZ(0.02turn);
  }
  94% {
    -webkit-transform: scale(1) translateY(0) rotateZ(-0.01turn);
            transform: scale(1) translateY(0) rotateZ(-0.01turn);
  }
  97% {
    -webkit-transform: scale(1) translateY(0) rotateZ(0.005turn);
            transform: scale(1) translateY(0) rotateZ(0.005turn);
  }
  100% {
    -webkit-transform: scale(1) translateY(0) rotateZ(0);
            transform: scale(1) translateY(0) rotateZ(0);
  }
}

@keyframes far_to_near {
  0% {
    -webkit-transform: scale(0) translateY(0) rotateZ(0);
            transform: scale(0) translateY(0) rotateZ(0);
  }
  80% {
    -webkit-transform: scale(1) translateY(-68px) rotateZ(0);
            transform: scale(1) translateY(-68px) rotateZ(0);
  }
  90% {
    -webkit-transform: scale(1) translateY(-48px) rotateZ(0.02turn);
            transform: scale(1) translateY(-48px) rotateZ(0.02turn);
  }
  94% {
    -webkit-transform: scale(1) translateY(0) rotateZ(-0.01turn);
            transform: scale(1) translateY(0) rotateZ(-0.01turn);
  }
  97% {
    -webkit-transform: scale(1) translateY(0) rotateZ(0.005turn);
            transform: scale(1) translateY(0) rotateZ(0.005turn);
  }
  100% {
    -webkit-transform: scale(1) translateY(0) rotateZ(0);
            transform: scale(1) translateY(0) rotateZ(0);
  }
}

.effect {
  -webkit-transform-origin: 50% 50% 0;
      -ms-transform-origin: 50% 50% 0;
          transform-origin: 50% 50% 0;
  -webkit-animation: far_to_near 2s cubic-bezier(.5, 1.05, .59, .99) 0s 1;
          animation: far_to_near 2s cubic-bezier(.5, 1.05, .59, .99) 0s 1;
}


