.home-elem-wrapper {
  position: relative;
  overflow: hidden;
}

.home-elem-content {
  overflow: hidden;
}

.home-elem-content > div {
  -webkit-transform-origin: 0 0 0;
      -ms-transform-origin: 0 0 0;
          transform-origin: 0 0 0;
}

.home-elem-wrapper, .home-elem-content, .home-elem-content > div {
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.home-elem-wrapper > h3 {
  line-height: 50px;
  text-indent: 1em;
  color: white;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
}

.home-elem-wrapper > h3:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(50%, transparent), to(rgba(255, 255, 255, 0.3)));
  background-image: -webkit-linear-gradient(top, transparent 0%, transparent 50%, rgba(255, 255, 255, 0.3) 100%);
  background-image: -o-linear-gradient(top, transparent 0%, transparent 50%, rgba(255, 255, 255, 0.3) 100%);
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, rgba(255, 255, 255, 0.3) 100%);
  color: blanchedalmond;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}


