.scroll-elem-wrapper {
  overflow: hidden;
}

.scroll-elem-content {
  overflow: hidden;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}


